//----------------------------------------------------
.avatar-holder{
    float: $text-direction;
}
.header-tools{
    float: $text-reverse;
}


//----------------------------------------------------
.toolsarea-level1{
    .breadcrumb{
        float: $text-direction;
    }
}
.tools-level1{
    float: $text-reverse;
}





